import { NgModule } from '@angular/core';
import { NgbModule, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { QuillModule } from 'ngx-quill';
import { UtilidadesModule } from '../utilidades/utilidades.module';
import { PagarcriptoComponent } from './pagarcripto/pagarcripto.component';
import { PagosRoutes } from './pagos.routing';
import { PagosService } from '../services/pagos.service';
import { ReservarService } from '../services/reservar.service';

@NgModule({
  declarations: [PagarcriptoComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(PagosRoutes),
    FormsModule,
    NgbModule,
    ReactiveFormsModule,
    QuillModule,
    UtilidadesModule
  ],
  providers: [PagosService, ReservarService, NgbModal]
})
export class PagosModule { }
