import { Routes } from '@angular/router';
import { PagarcriptoComponent } from './pagarcripto/pagarcripto.component';

export const PagosRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'pagobitcoint/:IdReservation',
        component: PagarcriptoComponent
      },
      {
        path: 'pagobitcoint',
        component: PagarcriptoComponent
      }
    ]
  }];