import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder } from '@angular/forms';
import { PagosService } from '../../services/pagos.service';
import { TipoMoneda } from '../../modelos/TipoMoneda';
import { ReservarService } from '../../services/reservar.service';
import { Reservation } from '../../modelos/Reservas/Reservation.model';
import { Observable } from 'rxjs';
import { GenericResponse } from '../../modelos/Response/GenericResponse.model';
import { GenericStorageService } from 'src/app/services/genericstorage.service';
import { CPRatesBaseResponse } from 'src/app/modelos/Response/CPRatesBaseResponse.model';
import { SimpleGenericResponse } from 'src/app/modelos/Response/SimpleGenericResponse.model';
import { CPRatesResponse } from 'src/app/modelos/Response/CPRatesResponse.model';
import { CPTransactionResponse } from 'src/app/modelos/Response/CPTransactionResponse.model';
import { CPTransactionBaseResponse } from 'src/app/modelos/Response/CPTransactionBaseResponse.model';
import { NgbProgressbarConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { InformacionBasicaComponent } from 'src/app/publicaciones/InformacionBasica/InformacionBasica.component';
import { IsProd } from '../../services/AppServiceGlobal';

@Component({
  selector: 'app-pagarcripto',
  templateUrl: './pagarcripto.component.html',
  styleUrls: ['./pagarcripto.component.scss'],
})
export class PagarcriptoComponent implements OnInit 
{
  isSpinnerVisiblePagando = false;
  isSpinnerVisible = true;
  mensaje: any = [];
  EsAmbienteProductivo: boolean = IsProd;

  CodigoMoneda: string = "BTC";
  TextoMonedaSeleccionada: string = "Bitcoin";
  TipoMoneda1:TipoMoneda;
  TipoMoneda2:TipoMoneda;
  TipoMoneda3:TipoMoneda;


  IdReservation: string;
  Reserva:Reservation = new Reservation;
  resultsConsultaMoneda$:Observable<SimpleGenericResponse<CPRatesResponse>>;
  resultsInicioTransaccion$:Observable<SimpleGenericResponse<CPTransactionResponse>>;

  constructor(private route: ActivatedRoute, public fb: FormBuilder,private router:Router,
    private pagoService:PagosService,
    private modalService: NgbModal,
    private genStorageServ : GenericStorageService<Reservation>,
    reservaservi:ReservarService) 
  { 
      this.IdReservation = this.route.snapshot.params.IdReservation;
      this.Reserva = this.genStorageServ.getInformation("InformacionParaPago");
      this.CargarInformacionMonedas();
  }

  ngOnInit() {}

  CargarInformacionMonedas()
  {
      this.resultsConsultaMoneda$ = this.pagoService.ConsultarListaMonedas();
      this.resultsConsultaMoneda$.subscribe(respuesta => 
      {
          if(respuesta != null && respuesta.IsProcesed && respuesta.JSonResult != null && respuesta.JSonResult.result != null)
          {
              var monedas: CPRatesBaseResponse = respuesta.JSonResult.result;
              this.ConfigurarMonedas(monedas);
          }

          this.isSpinnerVisible = false;
      },
      error => 
      {
          this.isSpinnerVisible = false;
      });
  }

  ConfigurarMonedas(monedas: CPRatesBaseResponse)
  {
      this.TipoMoneda1 = new TipoMoneda();
      this.TipoMoneda1.name = 'Bitcoin';
      this.TipoMoneda1.url = 'assets/images/Pagos/BTC.png';
      this.TipoMoneda1.Valor = Number(monedas.USD.rate_btc) * Number(this.Reserva.ValorContrato); //(valor * 0.00014);
     
      this.TipoMoneda2 = new TipoMoneda();
      this.TipoMoneda2.name = 'Litecoin';
      this.TipoMoneda2.url = 'assets/images/Pagos/LTC.png';
      this.TipoMoneda2.Valor = ( Number(monedas.USD.rate_btc) / Number(monedas.LTC.rate_btc) ) * Number(this.Reserva.ValorContrato); //(valor * 0.011);
     
      this.TipoMoneda3 = new TipoMoneda();
      this.TipoMoneda3.name = "LTCT Test";
      this.TipoMoneda3.url = 'assets/images/Pagos/LTC.png';
      this.TipoMoneda3.Valor = ( Number(monedas.USD.rate_btc) / Number(monedas.LTCT.rate_btc) ) * Number(this.Reserva.ValorContrato); //(valor * 0.011);
  }

  EnviarPago()
  {
      this.isSpinnerVisiblePagando = true;
      var itemName: string  = this.Reserva.IdServiceType + " - " + this.Reserva.IdSpace +  " / " + this.Reserva.IdService; 

      this.resultsInicioTransaccion$ = this.pagoService.IniciarTransaccion(this.Reserva.ValorContrato.toString(), itemName, this.Reserva.IdReservation, this.CodigoMoneda);
      this.resultsInicioTransaccion$.subscribe(respuesta => 
      {
          if(respuesta != null && respuesta.IsProcesed && respuesta.JSonResult != null && respuesta.JSonResult.result != null)
          {
            var infoTransaccion: CPTransactionBaseResponse = respuesta.JSonResult.result;
            this.mensaje = [ {id:1, type:'success', message: "La operaci贸n se ha realizado exit贸samente. Su Id de transacci贸n es el siguiente: " + infoTransaccion.txn_id + "!" }];
            console.log(infoTransaccion);
            
            window.open(infoTransaccion.checkout_url,"_blank");
          }
          else
          {
            this.mensaje = [{ id:1, type:'warning', message: "Estamos presentando inconvenientes con el sistema de pagos. Por favor intente nuevamente!" }];
          }

          this.isSpinnerVisiblePagando = false;
      },
      error => 
      {
          this.isSpinnerVisiblePagando = false;
      });
  }

  MostrarConfirmacion(content3)
  {
      this.modalService.open(content3, { size: 'sm' });
  }

  AceptarModalConfirmacionTransaccion()
  {
      this.modalService.dismissAll('Close click');
  }

  Regresar()
  {
        this.router.navigate(["/reservas/MisReservas"]);
  }

  SeleccionarMoneda(codigoMoneda)
  {
        this.CodigoMoneda = codigoMoneda;

        if(this.CodigoMoneda == "BTC")
            this.TextoMonedaSeleccionada = "Bitcoin";
        else if(this.CodigoMoneda == "LTC")
            this.TextoMonedaSeleccionada = "Litecoin";
        else if(this.CodigoMoneda == "LTCT")
            this.TextoMonedaSeleccionada = "Litecoin - Testing";
  }
}
