import { Injectable } from '@angular/core';
import { UrlBackendServices } from './AppServiceGlobal';
import { StorageServiceService } from './storage-service.service';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SimpleGenericResponse } from '../modelos/Response/SimpleGenericResponse.model';
import { CPRatesResponse } from '../modelos/Response/CPRatesResponse.model';
import { CPTransactionResponse } from '../modelos/Response/CPTransactionResponse.model';

@Injectable({
  providedIn: 'root'
})
export class PagosService {
  
  private ulrl = UrlBackendServices + '/api';
  constructor(private http: HttpClient, private storageServ: StorageServiceService) { }

  ConsultarListaMonedas(): Observable<SimpleGenericResponse<CPRatesResponse>>
  {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' });
    const consulta = 
    {
        ApplicationId     : this.storageServ.getCurrentApplication(),
        Token             : this.storageServ.getCurrentToken()
    };

    return this.http.post<SimpleGenericResponse<CPRatesResponse>>(this.ulrl + '/PagoReservas', consulta, { headers: headers });
  }

  IniciarTransaccion(Amount: string, ItemName: string, ItemNumber: string, SelectedCurrency: string): Observable<SimpleGenericResponse<CPTransactionResponse>>
  {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' });
    const consulta = 
    {
        ApplicationId     : this.storageServ.getCurrentApplication(),
        Token             : this.storageServ.getCurrentToken(),
        Amount            : Amount, 
        Currency1         : "USD",
        Currency2         : SelectedCurrency,
        ItemName          : ItemName,
        ItemNumber        : ItemNumber
    };

    return this.http.put<SimpleGenericResponse<CPTransactionResponse>>(this.ulrl + '/PagoReservas', consulta, { headers: headers });
  }
}
